body {
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 30px;
  letter-spacing: 0.05em;
  font-weight: 200;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.title-lg {
  font-family: "Panton", Helvetica, Arial, sans-serif;
  font-size: 60px;
  line-height: 72px;
  letter-spacing: 0.25em;
  font-weight: 600;
}

.title-md {
  font-family: "Panton", Helvetica, Arial, sans-serif;
  font-size: 48px;
  line-height: 60px;
  letter-spacing: 0.25em;
  font-weight: 600;
}

.title-sm {
  font-family: "Panton", Helvetica, Arial, sans-serif;
  font-size: 30px;
  line-height: 48px;
  letter-spacing: 0.25em;
  font-weight: 600;
}

.title-xs {
  font-family: "SFUIDisplay", Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.05em;
  font-weight: 200;
}

.title-xs_small {
  font-size: 14px;
  font-weight: 600;
}

.inner {
  max-width: 1045px;
  padding: 0 20px;
  margin: 0 auto;
}

.logo {
  width: 97px;
  display: block;
}

.logo img {
  max-width: 100%;
  display: block;
}

.footer {
  padding: 55px 0 70px;
  background: #fafafa;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
