// FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//Panton
//Black
@font-face {
	@include fontdefault(normal, 900, 'Panton');
	@include fontpath('Panton-Black', 'Panton-Black', 'Panton-Black'); }

//SemiBold
@font-face {
	@include fontdefault(normal, 600, 'Panton');
	@include fontpath('Panton SemiBold', 'Panton SemiBold', 'Panton SemiBold'); }

//SFUIDisplay
//Bold
@font-face {
	@include fontdefault(normal, 600, 'SFUIDisplay');
	@include fontpath('SFUIDisplay-Bold', 'SFUIDisplay-Bold', 'SFUIDisplay-Bold'); }

//Light
@font-face {
	@include fontdefault(normal, 200, 'SFUIDisplay');
	@include fontpath('SFUIDisplay-Light', 'SFUIDisplay-Light', 'SFUIDisplay-Light'); }

//Medium
@font-face {
	@include fontdefault(normal, 500, 'SFUIDisplay');
	@include fontpath('SFUIDisplay-Medium', 'SFUIDisplay-Medium', 'SFUIDisplay-Medium'); }
